.header {
  background-color: transparent;
  position: absolute;
  width: 100%;
  top: 0;

  .container {
    display: flex;
    justify-content: space-between;
    padding: 28px 29px;
  }

  &__logo {
    width: 164px;
    height: 84px;
  }

  &__callback-btn {
    display: flex;
    align-items: center;
    background-color: transparent;
    border: 1px solid $color-splash;
    color: $color-splash;
    font-weight: 500;
    font-size: 13px;
    line-height: normal;
    border-radius: 10px;
    margin-top: 10px;
    padding: 4px 12px;
    cursor: pointer;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }

    & svg {
      fill: $color-splash;
    }
  }
}
