.contacts {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__link {
    color: $color-silver;
    font-weight: 600;
    font-size: 20px;
    line-height: normal;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }
}