.goods {
  position: relative;
  z-index: 1;
  background-color: $color-dark-tangerine;
  padding: 50px 0;
  
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 45px;
  }

  &__item {

  }

  &__item-wrap {

  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }

  &__picture {
    border-radius: 5px;
    border: 2px solid $color-nero;
    padding: 7px;

    & img {
      border-radius: 5px;
      width: 195px;
      height: 233px;
      object-fit: cover;
    }
  }

  &__price {
    font-weight: 500;
    font-size: 22px;
    line-height: 0.95;
    text-align: center;
    filter: drop-shadow(2px 5px 2px rgba($color-nero, 30%));
    margin: 0;
  }

  &__mask {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 90%;
    transform: translate(-50%, -50%);
    z-index: -1;
  
    & svg {
      width: 100%;
      height: 100%;
    }
  }
}