.works {
  background-color: $color-nero;
  padding: 90px 0;

  &__title {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-bottom: 50px;
    max-width: 420px;

    &::after {
      content: '';
      width: 100%;
      height: 2px;
      background: linear-gradient(
        to right,
        $color-nero 0 5%,
        $color-splash 55% 60%,
        $color-nero 95% 100%,
      );
      margin-top: 15px;
    }
  }


  &__wrap {
    position: relative;
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__list {
    overflow: hidden;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 30px;
    justify-content: center;
    
  }

  &__item {
    padding: 10px;
  }

  &__picture {
    
    // 
    box-shadow: 0 4px 8px 3px rgba($color-splash, 16%);

    & img {
      width: 140px;
      height: 190px;
      object-fit: cover;
    }
  }

  
}


.swiper-button-prev {
  &:after {
    color: $color-splash;
  }
}

.swiper-button-next {
  &:after {
    color: $color-splash;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }
}

.swiper-button-next,
.swiper-button-prev {
  transform: translateY(-65%);
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: rgba($color-splash, 40%);
  border-radius: 50%;
}





