@keyframes activateMarker {
  0% {
    opacity: 1;
    transform: scale(0.1);
  }
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}
.marker {
  transform: translate(-3px, -28px);
}
.marker__pin {
  width: 66px;
  height: 101px;
  position: absolute;
  margin: -4em 0 0 -2em;
  padding: 0 3px 3px 0;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.marker__beacon {
  position: absolute;
  z-index: -1;
  width: 3em;
  transform: translateX(-50%);
}
.marker__animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 3em;
  height: 3em;
  content: "";
  border-radius: 50%;
  box-shadow: 0 0 0 3px #3498db;
  transform: scale(0.1);
  animation: activateMarker 2s infinite linear;
}
.marker__animation-delay {
  animation-delay: 1s;
}

