.advantages {
  position: relative;
  padding-top: 50px;
  padding-bottom: 200px;
  background-color: $color-nero;

  // background: radial-gradient(circle at center,
  //     #f5f5f5,
  //     #191919);

  // background: radial-gradient(circle at center,
  //     #292929 0%,
  //     #191919 50%);

  /* Размеры и центрирование контента */
  // min-height: 100vh;
  // display: flex;
  // // flex-direction: column;
  // align-items: center;
  // justify-content: center;
  // padding: 2rem;


  @include vp-1023 {
    padding-bottom: 130px;
  }

  &__box {
    max-width: 900px;
    margin: 0 auto;

    display: grid;
    gap: 20px;
    grid-template-columns: repeat(12, 1fr);

    @include vp-1023 {
      grid-template-columns: repeat(6, 1fr);
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      gap: 30px;
    }
  }

  &__item {
    max-width: 200px;
    position: relative;

    &::before {
      width: 2px;
      height: 70px;
      background-color: $color-dark-tangerine;
      border-radius: 12px;
    }

    &-1 {
      grid-column: 1 / span 4;
      text-align: right;

      @include vp-1279 {
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 1 / span 3;
      }

      @include vp-767 {
        order: -4;
      }
    }

    &-2 {
      grid-column: 1 / span 4;
      text-align: right;
      grid-row: 2;

      @include vp-1279 {
        grid-column: 5 / span 4;
        grid-row: 1;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-row: 6;
        grid-column: 4 / span 3;
      }

      @include vp-767 {
        order: -3;
      }
    }

    &-3 {
      grid-column: 1 / span 4;
      text-align: right;
      grid-row: 3;

      @include vp-1279 {
        grid-column: 9 / span 4;
        grid-row: 1;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 4 / span 3;
      }

      @include vp-767 {
        order: -2;
      }
    }

    &-4 {
      grid-column: 1 / span 4;
      grid-row: 4;
      text-align: right;

      @include vp-1279 {
        grid-column: 1 / span 4;
        grid-row: 2;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 1 / span 3;
      }
    }

    &-5 {
      margin-top: 100px;

      grid-row: 4;
      grid-column: 5 / span 4;
      justify-self: center;

      @include vp-1279 {
        grid-column: 1 / span 4;
        grid-row: 3;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 1 / span 3;
      }
    }

    &-6 {
      grid-row: 1;
      grid-column: 10 / span 3;
      justify-self: end;

      @include vp-1279 {
        grid-column: 1 / span 4;
        grid-row: 4;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 1 / span 3;
      }
    }

    &-7 {
      grid-row: 2;
      grid-column: 10 / span 3;
      justify-self: end;

      @include vp-1279 {
        grid-column: 1 / span 4;
        grid-row: 5;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 1 / span 3;
      }
    }

    &-8 {
      grid-row: 3;
      grid-column: 10 / span 3;
      justify-self: end;

      @include vp-1279 {
        grid-column: 5 / span 4;
        grid-row: 5;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-column: 4 / span 3;
      }
    }

    &-9 {
      grid-row: 4;
      grid-column: 10 / span 3;
      justify-self: end;

      @include vp-1279 {
        grid-column: 9 / span 4;
        grid-row: 5;
        text-align: center;
        justify-self: center;
      }

      @include vp-1023 {
        grid-row: 6;
        grid-column: 1 / span 3;
      }
    }

    // &-12 {
    //   grid-row: 5;
    //   grid-column: 10 / span 3;
    //   justify-self: end;

    //    @include vp-1279 {
    //     grid-column: 7 / span 6;
    //     grid-row: 3;
    //     text-align: center;
    //     justify-self: center;
    //   }
    // }

    &-1 {
      &::before {
        content: "";
        position: absolute;
        top: -3px;
        right: -103px;
        transform: rotate(-50deg);
      }
    }

    &-4 {
      &::before {
        content: "";
        position: absolute;
        top: -47px;
        right: -103px;
        transform: rotate(50deg);
      }
    }

    &-5 {
      & .advantages__item-title {
        &::before {
          top: -100%;
          left: 50%;
        }

        &::after {
          top: -200%;
          left: 46%;
          transform: rotate(90deg);
        }
      }

      & .advantages__item-title,
      & .advantages__item-text  {
        text-align: center;
      }
    }

    &-6,
    &-7,
    &-8,
    &-9 {
      & .advantages__item-title {
        &::before {
          left: -50px;
          top: 50%;
          transform: translateY(-50%);
        }

        &::after {
          top: 50%;
          transform: translateY(-50%);
          left: -100px;
          width: 50px;
          height: 2px;
          background-color: $color-dark-tangerine;
        }
      }
    }

    &-6 {
      &::before {
        content: "";
        position: absolute;
        top: -4px;
        left: -126px;
        transform: rotate(50deg);
      }
    }

    &-9 {
      &::before {
        content: "";
        position: absolute;
        top: -47px;  
        left: -126px;
        transform: rotate(-50deg);
      }
    }

    @include vp-1279 {
      &::before {
        content: none;
      }

      & .advantages__item-title {
        &::before {
          content: none;
        }

        &::after {
          content: none;
        }
      }
    }

    @include vp-1023 {
      align-self: center;
    }
  }

  &__item-title {
    position: relative;
    font-weight: 500;
    font-size: 14px;
    line-height: normal;
    color: $color-dark-tangerine;
    margin: 0;
    margin-bottom: 5px;

    &::before {
      content: "";
      position: absolute;
      right: -50px;
      top: 50%;
      transform: translateY(-50%);
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: $color-dark-tangerine;
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -75px;
      width: 25px;
      height: 2px;
      background-color: $color-dark-tangerine;
    }
  }

  &__item-text {
    font-weight: 500;
    line-height: 0.95;
    font-size: 11px;
    color: $color-silver;
    margin: 0;
  }

  &__picture {
    grid-row: 1 / span 3;
    grid-column: 5 / span 4;
    justify-self: center;

    & img {
      max-height: 600px;
    }

    @include vp-1279 {
      grid-column: 7 / span 6;
      grid-row: 2 / span 3;
      text-align: center;
      justify-self: center;
    }

    @include vp-1023 {
      grid-column: 4 / span 3;
    }

    @include vp-767 {
      order: -1;
      justify-content: center;

      & img {
        margin: auto;
        max-height: 400px;
      }
    }
  }

}