.faq {
  background-color: $color-nero;
  padding: 50px 0;

  &__box {
    max-width: 350px;
    margin: 0 auto
  }

  &__title {
    color: $color-dark-tangerine;
    font-size: 24px;
    line-height: 0.95;
    margin: 0;
    margin-bottom: 30px;
    text-align: center;
  }

  &__list {
    margin: 0;
    margin-bottom: 28px;
    padding: 0;
    list-style: none;
  }

  &__item {
    // background-color: $color-silver;
    margin-bottom: 25px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__element {
    &.is-active {
      & .faq__btn {
        border-bottom: transparent;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;

        & svg {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: $color-silver;
    color: $color-shuttle-grey;
    border-radius: 5px;
    border: 2px solid $color-shuttle-grey;
    padding: 18px;
    font-size: 14px;
    line-height: 12px;
    cursor: pointer;
    transition: opacity $trans-default;

    & svg {
      transition: transform $trans-default;
      width: 10px;
      height: 5px;
    }

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  &__text {
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    padding: 18px;
    background-color: $color-silver;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border: 2px solid $color-shuttle-grey;
    border-top: none;

    & p {
      margin: 0;
    }
  }

  &__more {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    color: $color-nero;
    background-color: $color-dark-tangerine;
    border: 1px solid transparent;
    border-radius: 5px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 7px 18px;
    cursor: pointer;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }
}