.intro {
  background-image: url("../img/content/intro.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: $color-nero;
  min-height: 561px;

  display: flex;
  margin: auto;
  justify-content: center;

  & .container {
    // display: flex;
    margin: auto;
  }

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: normal;
    color: $color-silver;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 30px;

    &--position {
      text-align: center;
    }
  }

  &__label {
    color: $color-splash;
  }
}