.footer {
  background-color: $color-nero;
  padding: 40px 0;

  &__row {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-bottom: 30px;
  }

  &__contacts {
    max-width: 500px;
  }

  &__contacts-list {
    list-style: none;
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
  }

  &__contacts-item {
    display: flex;
    align-content: center;
    margin-bottom: 30px;

    & svg {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      margin-right: 15px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__contacts-text {
    font-weight: 500;
    font-size: 16px;
    line-height: normal;
    color: $color-silver;
  }

  &__contacts-links {
    display: flex;
    flex-wrap: wrap;
  }

  &__contacts-link {
    width: 100%;
    font-weight: 500;
    font-size: 16px;
    line-height: normal;
    color: $color-silver;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  &__callback {
    max-width: 255px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 18px;
    line-height: 15px;
    color: $color-nero;
    background-color: $color-dark-tangerine;
    border: 1px solid transparent;
    border-radius: 5px;
    margin: 0;
    padding: 14px 18px;
    cursor: pointer;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  &__social {
    align-self: flex-end;
  }

  &__social-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 30px;
  }

  &__social-item {

  }

  &__social-link {
    transition: opacity $trans-default;
    
    & svg {
      width: 32px;
      height: 32px;
      border-radius: 50px;
    }

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  &__copyright {
    font-size: 12px;
    line-height: normal;
    font-weight: 500px;
    color: $color-silver;
    text-align: center;
    margin: 0;
  }
}
