.swiper {
  width: 600px;
  height: 260px;
}


.contacts {
  &--type-1 {
    background-color: $color-white-smoke;
    padding: 40px 0;
  }

  &__box {
    display: flex;
    justify-content: space-between;

    @include vp-1023 {
      display: block;
    }
  }

  &__column {
    margin-bottom: 30px;
  }

  &__data-column {
    @include vp-1023 {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  &__map-column {
    width: 70%;

    @include vp-1023 {
      width: 100%;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    &:last-of-type {
      margin-bottom: 0;
    }

    & svg {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {}

  &__text {
    margin: 0;
  }

  &__link {}

  &__mapbox {
    position: relative;
    width: 100%;
    height: 457px;
    background-color: $color-island-spice;

    @include vp-767 {
      transform: translateX(-50px);
    }

    @include vp-1279 {
      width: 100%;
      transform: none;
      grid-area: 1 / 7 / 7 / 13;
    }
  }
}

