.slider {
  &__btn {
    width: 19px;
    height: 35px;
    padding: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;

    &--prev {
      width: 19px;
      height: 35px;
      padding: 0;
      background-color: transparent;
      border: none;
      cursor: pointer;
      transform: rotate(180deg);
    }

    &--next {
      width: 19px;
      height: 35px;
      padding: 0;
      background-color: transparent;
      border: none;
      cursor: pointer;
    }
  }

  &__pagination {}

  &__pagination-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    gap: 7px;
  }

  &__pagination-item {
    width: 10px;
    height: 10px;
    background-color: rgba($color-splash, 40%);
    border-radius: 50%;

    &--active {
      background-color: rgba($color-splash, 100%);
    }
  }

}




